.l-navbar {
  position: fixed;
  top: 0;
  left: -30%;
  width: var(--nav-width);
  height: 100vh;
  background-color: var(--light-color);
  box-shadow: inset -1px 0px 0px rgba(0, 0, 0, 0.08);
  padding: 0;
  transition: .5s;
  z-index: var(--z-fixed);
  .active {
    color: var(--balck-color);
    background: linear-gradient(270deg, #eff7ff 0%, #ffffff 97.81%);

    .nav__name {
      color: #1f2128;
    }

    .icon__navbar {
      width: 20px;
      height: 20px;

      svg {
        path {
          fill: var(--blue-color);
        }
      }
    }

    &::before {
      content: '';
      position: absolute;
      right: 0;
      width: 3px;
      height: 100%;
      background-color: var(--blue-color);
    }
  }
}

.nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;

  &__logo, &__link {
    display: grid;
    grid-template-columns: max-content max-content;
    align-items: center;
    column-gap: 1.4rem;
    padding: 15px 20px 15px 25px;
  }

  .icon__navbar {
    width: 20px;
    height: 20px;

    svg {
      path {
        fill: var(--grey-color);
      }
    }
  }

  &__logo {
    margin-bottom: 2rem;

    &_icon {
      width: 25px;
    }

    &-name {
      width: 100%;
    }
  }

  &__link {
    position: relative;
    color: var(--first-color-light);
    margin-bottom: 8px;
    transition: .3s;

    &:hover {
      color: var(--balck-color);
    }
  }

  .nav__name {
    font-weight: 700;
    font-size: 11px;
    line-height: 20px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: var(--grey-color);
  }

}
