.issued__invoices {
  height: 100%;

  .issued__invoices__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px 12px 20px;
    box-shadow: inset 0px -1px 0px rgba(21, 24, 48, 0.08);


    .top_select {
      display: flex;

      .select_wrapper {
        width: 200px;
        position: relative;

        .select {
          display: flex;
          align-items: center;
          border: 1px solid #f2f6fa;
          border-radius: 6px;
          padding: 6px 8px;

          .select__dropdown {
            width: 100%;
            margin: 0 10px 0 10px;

            span {
              font-weight: 400;
              font-size: 10px;
              line-height: 12px;
              letter-spacing: 0.12em;
              text-transform: uppercase;
              color: var(--grey-color);
            }

            p {
              margin: 0;
              font-weight: 600;
              font-size: 12px;
              line-height: 16px;
              letter-spacing: 0.02em;
              color: #00154a;
            }

          }

          &:hover {
            cursor: pointer;
          }
        }

        .select_option18 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option19 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option20 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option_active {
          visibility: visible;
        }

        &:not(:last-child) {
          margin-right: 12px;
        }

      }

    }

  }

  .issued__invoices__wrapper-card {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 5px;
    grid-row-gap: 10px;
    margin-top: 18px;
    padding: 0 20px 0 20px;
    height: calc(100% - 105px);

    .issued__invoices__wrapper-card-item {
      height: 100%;
      display: flex;
      flex-direction: column;
      border: 1px solid #eef0f3;
      border-radius: 4px;

      .card__item-head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #eef0f3;
        padding: 5px 15px;

        .card__item-left {
          p {
            margin: 0;
            font-weight: 600;
            font-size: 12px;
            line-height: 20px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #00288d;

          }
        }


        .card__item-right {
          display: flex;
          align-items: center;

          img {
            &:hover {
              cursor: pointer;
            }

            &:not(:last-child) {
              margin-right: 20px;
            }
          }
        }

      }

      .card__item-body {
        border: 1px solid #eef0f3;
        border-radius: 4px;

        .card__item-body-item {
          display: flex;
          width: 100%;
          border-bottom: 1px solid #f2f6fa;

          .item__title {
            padding: 4px 8px;
            flex: 1;
            background-color: #f8f8f9;
            box-shadow: inset 1px 0 0 #e8e8ee;

            span {
              font-weight: 600;
              font-size: 11px;
              line-height: 16px;
              letter-spacing: 0.04em;
              text-transform: uppercase;
              color: #757d93;
            }
          }

          .item {
            padding: 4px 8px;
            flex: 1;

            span {
              font-weight: 600;
              font-size: 11px;
              line-height: 16px;
              letter-spacing: 0.04em;
              text-transform: uppercase;
              color: #757d93;
            }

            .red__text {
              color: #cc3030;
            }

            .user__name {
              color: #1f2128;
            }

            .user__info {
              color: #00154a;
            }
          }


          &:nth-child(3n) {
            background: #f8f8f9; /* Цвет фона */
          }
        }

        .bg__blue {
          background-color: #00288d;

          .item {
            span {
              color: #fff;
            }
          }
        }
      }


    }

  }

}
