.day-trick {
  height: 100%;

  .day-trick_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 25px 0 25px;

    .day-top_select {
      display: flex;

      .select_wrapper {
        width: 180px;
        position: relative;

        .select {
          display: flex;
          align-items: center;
          border: 1px solid #f2f6fa;
          border-radius: 6px;
          padding: 6px 8px;

          .select__dropdown {
            width: 100%;
            margin: 0 10px 0 10px;

            span {
              font-weight: 400;
              font-size: 10px;
              line-height: 12px;
              letter-spacing: 0.12em;
              text-transform: uppercase;
              color: var(--grey-color);
            }

            p {
              margin: 0;
              font-weight: 600;
              font-size: 12px;
              line-height: 16px;
              letter-spacing: 0.02em;
              color: #00154a;
            }

          }

          &:hover {
            cursor: pointer;
          }
        }

        .select_option28 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option29 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option_active {
          visibility: visible;
        }

        &:not(:last-child) {
          margin-right: 12px;
        }

      }

      @media screen and (max-width: 1366px) {
        .select_wrapper {
          width: 140px;

          .select_option28 {

            p {
              padding: 10px;
            }
          }

          .select_option29 {
            p {
              padding: 10px;
            }
          }
        }
      }


      .wrapper {
        width: 370px;
        margin: 85px auto 0;
      }

      .select-btn, li {
        display: flex;
        align-items: center;
        cursor: pointer;
      }

      .select-btn {
        height: 65px;
        padding: 0 20px;
        font-size: 22px;
        background: #fff;
        border-radius: 7px;
        justify-content: space-between;
        box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
      }

      .select-btn i {
        font-size: 31px;
        transition: transform 0.3s linear;
      }

      .wrapper.active .select-btn i {
        transform: rotate(-180deg);
      }

      .content {
        display: none;
        padding: 20px;
        margin-top: 15px;
        background: #fff;
        border-radius: 7px;
        box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
      }

      .wrapper.active .content {
        display: block;
      }

      .content .search {
        position: relative;
      }

      .search i {
        top: 50%;
        left: 15px;
        color: #999;
        font-size: 20px;
        pointer-events: none;
        transform: translateY(-50%);
        position: absolute;
      }

      .search input {
        height: 50px;
        width: 100%;
        outline: none;
        font-size: 17px;
        border-radius: 5px;
        padding: 0 20px 0 43px;
        border: 1px solid #B3B3B3;
      }

      .search input:focus {
        padding-left: 42px;
        border: 2px solid #4285f4;
      }

      .search input::placeholder {
        color: #bfbfbf;
      }

      .content .options {
        margin-top: 10px;
        max-height: 250px;
        overflow-y: auto;
        padding-right: 7px;
      }

      .options::-webkit-scrollbar {
        width: 7px;
      }

      .options::-webkit-scrollbar-track {
        background: #f1f1f1;
        border-radius: 25px;
      }

      .options::-webkit-scrollbar-thumb {
        background: #ccc;
        border-radius: 25px;
      }

      .options::-webkit-scrollbar-thumb:hover {
        background: #b3b3b3;
      }

      .options li {
        height: 50px;
        padding: 0 13px;
        font-size: 21px;
      }

      .options li:hover, li.selected {
        border-radius: 5px;
        background: #f2f2f2;
      }

    }

    .day-top_calendar__number {
      .calendar_swiper {
        display: flex;
        align-items: center;

        .swiper-wrapper {
          display: flex;
          align-items: center;
          margin: 0 20px 0 20px;

          .swiper-slide {
            background-color: var(--light-color);
            border-radius: 4px;
            border: 1px solid rgba(245, 246, 252, 1);
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            color: var(--grey-color);
            padding: 12px;

            &:not(:last-child) {
              margin-right: 4px;
            }

            &.active_calendar {
              border: 1px solid #ff6700;
              filter: drop-shadow(0px 4px 4px rgba(81, 124, 234, 0.1));
              border-radius: 4px;
              color: #ff6700;
            }

            &:hover {
              cursor: pointer;
            }
          }

          @media screen and (max-width: 1366px) {
            .swiper-slide {
              padding: 10px;
            }
          }
        }

        .btn {
          width: 40px;
          height: 40px;
          background-color: var(--light-color);
          box-shadow: 0 4px 4px rgba(81, 124, 234, 0.1);
          border-radius: 8px;
          transform: matrix(-1, 0, 0, 1, 0, 0);
          display: flex;
          align-items: center;
          justify-content: center;

          &:hover {
            cursor: pointer;
          }
        }

        @media screen and (max-width: 1366px) {
          .btn {
            width: 35px;
            height: 35px;
          }
        }
      }
    }

    @media screen and (max-width: 1366px) {
      .btn_top {
        padding: 10px;
      }
    }

  }

  .day-trick_calendar {
    margin-top: 20px;
    height: calc(100% - 100px);
    overflow-y: scroll;

    .day-trick_calendar__header {
      background-color: #f2f6fa;
      padding: 2px 0 2px 0;
      display: flex;
      align-items: center;

      .item {
        flex: 1;
        text-align: center;

        p {
          margin: 0;
          font-weight: 600;
          font-size: 12px;
          line-height: 14px;
          letter-spacing: 0.12em;
          text-transform: uppercase;
          color: #1F2128;
        }

        .text__active {
          color: #ff6700;
        }
      }
    }

    .day-trick_calendar__body {
      display: flex;

      .body-card {
        width: 17%;
        border-right: 1px solid #f8f8f9;
        border-bottom: 1px solid #f8f8f9;
        padding: 8px;
        min-height: 350px;

        .body-card__wrapper {
          border: 1px solid #dcdee4;

          .card-user {
            background-color: #f8f8f9;
            border-radius: 2px;

            .card-user__info {
              display: flex;
              align-items: center;
              background-color: #676f84;
              padding: 3px;

              img {
                display: block;
                width: 24px;
                height: 24px;
                background-color: var(--light-color);
                border-radius: 2px;
                object-fit: cover;
              }

              h3 {
                margin: 0 0 0 8px;
                font-weight: 600;
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.08em;
                text-transform: uppercase;
                color: var(--light-color);
              }
            }
          }

          .card-info__item {
            border-bottom: 1px solid #f2f6fa;
            padding: 4px;

            h4 {
              margin: 0;
              font-weight: 600;
              font-size: 11px;
              line-height: 14px;
              letter-spacing: 0.04em;
              text-transform: uppercase;
              color: #3b3f4b;
            }

            p {
              margin: 0;
              font-weight: 400;
              font-size: 10px;
              line-height: 12px;
              color: #878ea1;
            }
          }
        }

      }
    }
  }


  .modals {

    .modal__title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);
      padding: 16px 24px;
    }

    .modal-overlay {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.7);
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100vh;
      width: 100%;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in-out;
      z-index: 999;

      h2 {
        margin: 0;
        font-weight: 600;
        font-size: 17px;
        line-height: 24px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: var(--balck-color);
      }

      img {
        cursor: pointer;
      }
    }

    .modal {
      background-color: var(--light-color);
      width: 570px;
      display: none;
    }

    .modal-overlay--visible {
      opacity: 1;
      visibility: visible;
      transition: all 0.3s ease-in-out;
    }

    .modal--visible {
      display: block;
    }

    .modal__patients {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-column-gap: 15px;
      grid-row-gap: 0;
      padding: 24px;
      box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);

      label {
        width: 100%;
        display: flex;
        flex-direction: column;
        font-weight: 600;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.12em;
        text-transform: uppercase;
        color: #999faf;

        .select_wrapper {
          position: relative;
          border: 1px solid #dcdee4;
          border-radius: 2px;
          margin-top: 5px;

          .select {
            .select__dropdown {
              display: flex;
              align-items: center;
              justify-content: space-between;
              padding-right: 15px;

              p {
                font-weight: 600;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.03em;
                color: #1f2128;
                margin: 0;
                padding: 15px;
              }
            }
          }

          .select_option3 {
            position: absolute;
            border: 1px solid #f2f6fa;
            width: 100%;
            top: 100%;
            bottom: -100%;
            height: auto;
            left: 0;
            z-index: 10;
            visibility: hidden;
            background-color: #fff;

            .select_option_item {
              background-color: #fff;
            }

            p {
              margin: 0;
              font-weight: 600;
              font-size: 14px;
              line-height: 16px;
              letter-spacing: 0.02em;
              color: #00154a;
              padding: 15px 10px 10px 24px;

              &:hover {
                cursor: pointer;
              }
            }
          }

          .select_option_active {
            visibility: visible;
          }

          &:hover {
            cursor: pointer;
          }
        }


      }

      .input__label {
        margin-top: 15px;
        font-weight: 600;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.12em;
        text-transform: uppercase;
        color: #999faf;

        input {
          margin-top: 5px;
          font-weight: 600;
          font-size: 14px;
          line-height: 20px;
          letter-spacing: 0.03em;
          color: #1f2128;
          padding: 15px;
          border: 1px solid #dcdee4;
          outline: none;

          &::placeholder {
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.03em;
            color: #bcc3cc;
          }
        }
      }

      .label1 {
        grid-area: 1 / 1 / 2 / 5;
      }

      .label2 {
        grid-area: 2 / 1 / 3 / 3;
      }

      .label3 {
        grid-area: 2 / 3 / 3 / 5;
      }

      .label4 {
        grid-area: 3 / 1 / 4 / 3;
      }


    }

    .modal__doctor {
      padding: 24px;
      box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);

      .label_select {
        width: 100%;
        display: flex;
        flex-direction: column;
        font-weight: 600;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.12em;
        text-transform: uppercase;
        color: #999faf;

        .select_wrapper {
          position: relative;
          border: 1px solid #dcdee4;
          border-radius: 2px;
          margin-top: 5px;

          .select {
            .select__dropdown {
              display: flex;
              align-items: center;
              justify-content: space-between;
              padding-right: 15px;

              p {
                font-weight: 600;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.03em;
                color: #1f2128;
                margin: 0;
                padding: 15px;
              }
            }
          }

          .select_option4 {
            position: absolute;
            border: 1px solid #f2f6fa;
            width: 100%;
            top: 100%;
            bottom: -100%;
            height: auto;
            left: 0;
            z-index: 10;
            visibility: hidden;
            background-color: #fff;

            .select_option_item {
              background-color: #fff;
            }

            p {
              margin: 0;
              font-weight: 600;
              font-size: 14px;
              line-height: 16px;
              letter-spacing: 0.02em;
              color: #00154a;
              padding: 15px 10px 10px 24px;

              &:hover {
                cursor: pointer;
              }
            }
          }

          .select_option_active {
            visibility: visible;
          }

          &:hover {
            cursor: pointer;
          }
        }

      }

      .modal__doctor_date {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 15px;

        .doctor__date {
          display: flex;
          flex-direction: column;

          .doctor__date_title {
            font-weight: 600;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 0.12em;
            text-transform: uppercase;
            color: #999faf;
            margin-bottom: 5px;
          }

          .date {
            width: 253px;

            .select_wrapper {
              position: relative;
              border: 1px solid #dcdee4;
              border-radius: 2px;
              padding: 15px;

              .select {
                .select__dropdown {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;

                  .select__dropdown_date {
                    display: flex;
                    align-items: center;
                  }

                  p {
                    font-weight: 600;
                    font-size: 14px;
                    line-height: 20px;
                    letter-spacing: 0.03em;
                    color: #1f2128;
                    margin: 0 0 0 5px;
                  }
                }
              }

              .select_option5 {
                position: absolute;
                width: 100%;
                top: 100%;
                bottom: -100%;
                height: auto;
                left: 0;
                z-index: 10;
                visibility: hidden;
                background-color: #fff;

                .select_option_item {
                  background-color: #fff;
                }

                p {
                  margin: 0;
                  font-weight: 600;
                  font-size: 14px;
                  line-height: 16px;
                  letter-spacing: 0.02em;
                  color: #00154a;
                  padding: 15px 10px 10px 24px;

                  &:hover {
                    cursor: pointer;
                  }
                }
              }

              .select_option_active {
                visibility: visible;
              }

              &:hover {
                cursor: pointer;
              }
            }
          }

          .doctor__date__icon {
            margin-left: auto;
          }

          .time {
            display: flex;
            align-items: center;
            padding: 15px;
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.03em;
            color: #1f2128;
            border: 1px solid #dcdee4;
          }

          .date__number {
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.03em;
            color: #0b193e;
            margin-left: 10px;
          }

          .doctor__date__time {
            display: flex;

            .time {
              width: 118px;

              &:not(:last-child) {
                margin-right: 15px;
              }
            }
          }
        }

      }

      .modal__doctor_comment {
        margin-top: 15px;

        label {
          display: flex;
          flex-direction: column;
          font-weight: 600;
          font-size: 12px;
          line-height: 14px;
          letter-spacing: 0.12em;
          text-transform: uppercase;
          color: #999FAF;

          textarea {
            margin-top: 5px;
            padding: 15px;
            font-weight: 500;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.03em;
            color: #C3C7D1;
            border: 1px solid #dcdee4;
            border-radius: 2px;
            resize: none;
            height: 100px;
            outline: none;

            &::placeholder {
              font-weight: 500;
              font-size: 14px;
              line-height: 20px;
              letter-spacing: 0.03em;
              color: #C3C7D1;
            }
          }
        }
      }
    }

    .modal__checkbox {
      padding: 15px 25px;
      box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);
      display: flex;
      justify-content: space-between;

      .modal__checkbox_toggle {
        display: flex;
        align-items: center;

        span {
          display: flex;
          align-items: center;
          font-weight: 600;
          font-size: 12px;
          line-height: 40px;
          letter-spacing: 0.12em;
          text-transform: uppercase;
          color: #384565;
        }

        input[type=checkbox] {
          height: 0;
          width: 0;
          visibility: hidden;
        }

        label {
          cursor: pointer;
          text-indent: -9999px;
          width: 42px;
          height: 24px;
          display: block;
          border-radius: 100px;
          background: #FF6700;
          position: relative;
          margin-left: 15px;
        }

        label:after {
          content: '';
          position: absolute;
          top: 2px;
          left: 2px;
          width: 20px;
          height: 20px;
          background: #fff;
          border-radius: 90px;
          transition: 0.3s;
        }

        input:checked + label {
          background: #FF6700;
          border-radius: 20px;
        }

        input:checked + label:after {
          left: calc(100% - 1px);
          transform: translateX(-100%);
        }

      }

      .modal__checkbox_btn {
        button {
          font-weight: 700;
          font-size: 12px;
          line-height: 16px;
          letter-spacing: 0.05em;
          text-transform: uppercase;
          padding: 12px 25px;
          outline: none;

          &:hover {
            cursor: pointer;
          }
        }

        .checkbox__btn_orange {
          background-color: #ff6700;
          box-shadow: 0 4px 4px rgba(81, 124, 234, 0.1);
          border-radius: 4px;
          color: #F4F4F4;
          border: none;
        }

        .checkbox__btn_outline {
          border: 1px solid rgba(245, 246, 252, 1);
          background-color: var(--light-color);;
          color: #c3c7d1;
        }
      }
    }

    .modal__footer {
      padding: 15px 25px;

      display: flex;
      justify-content: flex-end;

      button {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        padding: 15px 20px;
        outline: none;

        &:hover {
          cursor: pointer;
        }

        &:not(:last-child) {
          margin-right: 10px;
        }
      }

      .btn__outline {
        background-color: var(--light-color);
        border: 1px solid #c3c7d1;
        border-radius: 2px;
        color: #c3c7d1;
      }

      .btn__blue {
        background-color: #00288d;
        border-radius: 2px;
        color: var(--light-color);
        border: none;
      }
    }
  }

}
