.employee {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
  background-color: var(--light-color);
  margin: 25px 35px 0 20px;

  &__search {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #EBF5FE;
    padding: 15px;
  }

  &__left {
    display: flex;
    align-items: center;
  }

  &__filter {
    margin-right: 10px;
    background-color: var(--light-color);
    padding: 12px;
    border: 1px solid var(--grey-border);

    &:hover {
      cursor: pointer;
    }
  }

  &__input {
    width: 300px;
    max-width: 100%;
    background-color: var(--light-color);
    border: 1px solid var(--grey-border);
    border-radius: 4px;
    padding: 10px;
    display: flex;
    align-items: center;

    input {
      width: 100%;
      border: none;
      outline: none;
      font-weight: 500;
      font-size: 12px;
      margin-left: 9px;
      line-height: 20px;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      color: var(--grey-color);

      &::placeholder {
        font-weight: 500;
        font-size: 12px;
        line-height: 20px;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: var(--grey-color);
      }
    }
  }

  &__right {

  }

  &__table {
    position: relative;

    &::-webkit-scrollbar {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      border-radius: 10px;
      background-color: #fff;
    }

    &::-webkit-scrollbar {
      width: 8px;
      background-color: var(--light-color);
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 10px;
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
      background-color: var(--grey-color);
    }

    .tex_red {
      background: rgba(223, 19, 19, 0.1);
      border-radius: 6px;
      padding: 8px 12px;
      font-weight: 700;
      font-size: 12px;
      line-height: 115%;
      letter-spacing: 0.01em;
      text-transform: uppercase;
      color: #f54940;
      display: inline-block;
    }

    .tex_green {
      background: rgba(51, 195, 36, 0.1);
      border-radius: 6px;
      padding: 8px 12px;
      font-weight: 700;
      font-size: 12px;
      line-height: 115%;
      letter-spacing: 0.01em;
      text-transform: uppercase;
      color: #33c324;
      display: inline-block;
    }

    table {
      width: 100%;

      td {
        padding: 15px;

        p {
          margin: 0;
          font-weight: 400;
          font-size: 14px;
          line-height: 115%;
          letter-spacing: 0.01em;
          color: var(--text-table);
        }
      }


      .filter_text {
        display: flex;
        align-items: center;

        span {
          font-weight: 700;
          font-size: 11px;
          line-height: 16px;
          letter-spacing: 0.04em;
          text-transform: uppercase;
          color: var(--grey-colortwo);
        }

        .filter_img {
          display: flex;
          align-items: center;
          flex-direction: column;
          margin-left: 3px;

          img {
            padding: 1px;

            &:not(:last-child) {
              margin-bottom: 1px;
            }

            &:hover {
              cursor: pointer;
            }
          }
        }
      }

      tr:nth-child(odd) {
        background-color: var(--whitegrey-color);
      }

    }

  }

  .employee__pagination {
    position: absolute;
    bottom: 30px;
    left: 0;
    width: 100%;
    background: rgba(244, 247, 252, 0.75);
    backdrop-filter: blur(8px);
    padding: 13px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .pagination__left {
      span {
        font-weight: 500;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.03em;
        color: var(--grey-colorthree);
      }
    }

    .pagination__right {
      display: flex;
      align-items: center;

      .pagination_right_select {
        margin-right: 25px;

        span {
          font-weight: 500;
          font-size: 12px;
          line-height: 18px;
          letter-spacing: 0.03em;
          color: var(--grey-colorthree);
        }

        select {
          background-color: transparent;
          border: none;
          font-weight: 500;
          font-size: 12px;
          line-height: 14px;
          letter-spacing: 0.03em;
          color: var(--grey-colorthree);
          outline: none;
        }
      }

      .pagination_right_btn {
        display: flex;
        align-items: center;

        .pagination_right_btn_number {
          margin: 0 10px;

          span {
            font-weight: 500;
            font-size: 12px;
            line-height: 18px;
            letter-spacing: 0.03em;
            color: #171C26;
          }
        }


        button {
          background-color: #f7f9fc;
          box-shadow: 0 0 0 1px rgba(70, 79, 96, 0.24);
          border-radius: 6px;
          border: none;
          width: 24px;
          height: 20px;
          display: flex;
          align-items: center;
          justify-content: center;


          &:hover {
            cursor: pointer;
          }

        }


        .button_active {
          background-color: #fff;
        }
      }
    }
  }

}
