.header {
  width: 100%;
  height: var(--header-height);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
  background: #fff;
  box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);
  z-index: var(--z-fixed);
  transition: .5s;

  &__toggle {
    color: var(--first-color);
    font-size: 1.5rem;

    p {
      margin: 0;
      font-weight: 600;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 0.02em;
      color: #283966;
    }
  }


  &__right {
    display: flex;
    align-items: center;
  }

  &__notif {
    margin-right: 35px;

    &:hover {
      cursor: pointer;
    }
  }

  &__user {
    display: flex;
    align-items: center;

    .header__img {
      width: 35px;
      height: 35px;
      display: flex;
      justify-content: center;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 5px;

      & img {
        width: 40px;
      }

    }

    &:hover {
      cursor: pointer;
    }
  }


}
