.price {
  display: flex;
  column-gap: 20px;

  &__left {
    width: 20%;

    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    &-list-head {
      background-color: #00154a;
      border-radius: 3px;
      padding: 15px;

      p {
        margin: 0;
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: var(--light-color);
      }
    }

    &-list-item {
      padding: 15px;
      background-color: #fafafc;
      border-radius: 2px;
      margin: 5px 0 5px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;

      p {
        margin: 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.02em;
        color: #757d93;
      }

      span {
        svg {
          fill: #fafafc;
          stroke: #adb2bf;
        }
      }

      &:hover {
        cursor: pointer;
      }

      &.active {
        background-color: #ff6700;

        span {
          svg {
            fill: #ff6700;
            stroke: #fff;
          }
        }

        p {
          color: var(--light-color);
        }
      }
    }
  }

  &__right {
    width: 80%;

    &-list {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    &-list-head {
      background-color: #00154a;
      border-radius: 3px;
      padding: 15px;

      p {
        margin: 0;
        font-weight: 700;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: var(--light-color);
      }
    }

    &-list-item {
      padding: 15px;
      background-color: var(--light-color);
      border-bottom: 1px solid #c3c7d1;;
      border-radius: 2px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      p {
        margin: 0;
        font-weight: 600;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.02em;
        color: #00154a;

        img {
          margin-right: 5px;
        }
      }

      span {
        font-weight: 700;
        font-size: 16px;
        line-height: 16px;
        letter-spacing: 0.02em;
        color: #00288d;
      }

      &:hover {
        cursor: pointer;
        background-color: #f9fafc;
      }

      &.active {
        background-color: #ff6700;

        p {
          color: var(--light-color);
        }
      }
    }
  }
}
