// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Roboto", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  // ----
  --grey-color: #adb2bf;
  --grey-colortwo: #464F60;
  --grey-colorthree: #687182;
  --grey-colorfour: #999faf;
  --balck-color: #1f2128;
  --blue-color: #0047F9;
  --whitegrey-color: #f2f6fa;
  --light-color: #fff;
  --grey-border: #dcdee4;
  --text-table: #00154a;
  --green-status-color: #edfbf2;
  --blue-status-color: #ebf5fe;
  --blue-table-color: #00288d;
  // -----
  --header-height: 3rem;
  --nav-width: 68px;

  /* ===== z index ===== */
  --z-fixed: 100;


}
