.notfound {
  height: calc(100% + 2.9rem);
  width: 100%;
  background-color: var(--light-color);
  position: absolute;
  top: -48px;
  left: 0;
  z-index: 999;
  background-image: url("../img/notFound.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 30%;
  display: flex;
  justify-content: center;
  align-items: center;

  .notfound_title {
    display: flex;
    flex-direction: column;
    justify-content: center;

    p {
      font-weight: 400;
      font-size: 14px;
      line-height: 140%;
      color: #00154a;
    }

    .btn_notfound {
      padding: 12px;
      background-color: #003fde;
      border-radius: 57px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 600;
      font-size: 11px;
      line-height: 20px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: #ffffff;

      img {
        margin-right: 5px;
      }
    }
  }

  .notfound__man {
    position: absolute;
    bottom: -2px;
    left: 30%;
    display: flex;
    align-items: center;
  }

  @media screen and (max-width: 1366px) {
    .notfound__man {
      left: 20%;
    }
  }
}
