.schedules {
  height: 100%;

  .schedules_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;

    .top_select {
      display: flex;

      .select_wrapper {
        width: 180px;
        position: relative;

        .select {
          display: flex;
          align-items: center;
          border: 1px solid #f2f6fa;
          border-radius: 6px;
          padding: 6px 8px;

          .select__dropdown {
            width: 100%;
            margin: 0 10px 0 10px;

            span {
              font-weight: 400;
              font-size: 10px;
              line-height: 12px;
              letter-spacing: 0.12em;
              text-transform: uppercase;
              color: var(--grey-color);
            }

            p {
              margin: 0;
              font-weight: 600;
              font-size: 12px;
              line-height: 16px;
              letter-spacing: 0.02em;
              color: #00154a;
            }

          }

          &:hover {
            cursor: pointer;
          }
        }

        .select_option11 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }


        .select_option_active {
          visibility: visible;
        }

        &:not(:last-child) {
          margin-right: 12px;
        }

      }

      @media screen and (max-width: 1366px) {
        .select_wrapper {
          width: 140px;

          .select_option11 {

            p {
              font-size: 12px;
              padding: 10px;

            }
          }


          .select_option_active {
            visibility: visible;
          }

          &:not(:last-child) {
            margin-right: 12px;
          }

        }
      }


      .wrapper {
        width: 370px;
        margin: 85px auto 0;
      }

      .select-btn, li {
        display: flex;
        align-items: center;
        cursor: pointer;
      }

      .select-btn {
        height: 65px;
        padding: 0 20px;
        font-size: 22px;
        background: #fff;
        border-radius: 7px;
        justify-content: space-between;
        box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
      }

      .select-btn i {
        font-size: 31px;
        transition: transform 0.3s linear;
      }

      .wrapper.active .select-btn i {
        transform: rotate(-180deg);
      }

      .content {
        display: none;
        padding: 20px;
        margin-top: 15px;
        background: #fff;
        border-radius: 7px;
        box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
      }

      .wrapper.active .content {
        display: block;
      }

      .content .search {
        position: relative;
      }

      .search i {
        top: 50%;
        left: 15px;
        color: #999;
        font-size: 20px;
        pointer-events: none;
        transform: translateY(-50%);
        position: absolute;
      }

      .search input {
        height: 50px;
        width: 100%;
        outline: none;
        font-size: 17px;
        border-radius: 5px;
        padding: 0 20px 0 43px;
        border: 1px solid #B3B3B3;
      }

      .search input:focus {
        padding-left: 42px;
        border: 2px solid #4285f4;
      }

      .search input::placeholder {
        color: #bfbfbf;
      }

      .content .options {
        margin-top: 10px;
        max-height: 250px;
        overflow-y: auto;
        padding-right: 7px;
      }

      .options::-webkit-scrollbar {
        width: 7px;
      }

      .options::-webkit-scrollbar-track {
        background: #f1f1f1;
        border-radius: 25px;
      }

      .options::-webkit-scrollbar-thumb {
        background: #ccc;
        border-radius: 25px;
      }

      .options::-webkit-scrollbar-thumb:hover {
        background: #b3b3b3;
      }

      .options li {
        height: 50px;
        padding: 0 13px;
        font-size: 21px;
      }

      .options li:hover, li.selected {
        border-radius: 5px;
        background: #f2f2f2;
      }

    }

    .top_calendar__number {
      .calendar_swiper {
        display: flex;
        align-items: center;

        .swiper-wrapper {
          display: flex;
          align-items: center;
          margin: 0 20px 0 20px;

          .swiper-slide {
            background-color: var(--light-color);
            border-radius: 4px;
            border: 1px solid rgba(245, 246, 252, 1);
            font-weight: 600;
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            color: var(--grey-color);
            padding: 12px;

            &:not(:last-child) {
              margin-right: 4px;
            }

            &.active_calendar {
              border: 1px solid #ff6700;
              filter: drop-shadow(0px 4px 4px rgba(81, 124, 234, 0.1));
              border-radius: 4px;
              color: #ff6700;
            }

            &:hover {
              cursor: pointer;
            }
          }

          @media screen and (max-width: 1366px) {
            .swiper-slide {
              padding: 10px;
            }
          }
        }

        .btn {
          width: 40px;
          height: 40px;
          background-color: var(--light-color);
          box-shadow: 0 4px 4px rgba(81, 124, 234, 0.1);
          border-radius: 8px;
          transform: matrix(-1, 0, 0, 1, 0, 0);
          display: flex;
          align-items: center;
          justify-content: center;

          &:hover {
            cursor: pointer;
          }
        }

        @media screen and (max-width: 1366px) {
          .btn {
            width: 35px;
            height: 35px;
          }
        }
      }
    }

  }


  .schedules__day {
    height: 100%;

    .schedules__day-title {
      display: flex;
      justify-content: space-between;
      column-gap: 10px;
      border-top: 1px solid #dcdee4;
      border-left: 1px solid #dcdee4;
      padding: 4px 30px 4px 55px;

      .title__item {
        display: flex;
        text-align: center;
        flex: 1;

        span {
          display: inline-block;
          width: 100%;
          padding: 2px 0 2px 0;
        }

        .text_blue {
          background-color: #f2f6fa;
          border-radius: 3px;
          font-weight: 600;
          font-size: 12px;
          line-height: 14px;
          letter-spacing: 0.12em;
          text-transform: uppercase;
          color: #0038C6;
        }

        .text__orange {
          font-weight: 600;
          font-size: 12px;
          line-height: 14px;
          letter-spacing: 0.12em;
          text-transform: uppercase;
          color: #ff6700;
          background-color: #ffdbc2;
          border-radius: 3px;
        }
      }
    }

    .schedules_calendar {
      height: 100%;
      position: relative;

      .schedules_calendar__header {
        display: flex;
        align-items: center;
        background-color: #f2f6fa;
        padding: 2px 30px 2px 0;
        position: relative;
        //width: calc(100% - 50px);
        //margin-left: 50px;

        .item_info {
          flex: 1;

          p {
            margin: 0;
            font-weight: 600;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 0.12em;
            text-transform: uppercase;
            color: #1F2128;
            text-align: center;
          }
        }

        .item_info_day {
          position: absolute;
          top: 0;
          left: 0;
          background-color: var(--light-color);
          width: 50px;
          height: 97%;
          display: flex;
          align-items: center;
          justify-content: center;
          box-shadow: inset 0px -1px 0px rgba(21, 24, 48, 0.08);

          p {
            margin: 0;
            font-weight: 600;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 0.12em;
            text-transform: uppercase;
            color: #1f2128;
          }
        }
      }

      .schedules_calendar__time-wrapper {
        height: 100%;
        border-left: 1px solid #dcdee4;

        .schedules_calendar_time {
          background-color: var(--light-color);
          display: flex;
          align-items: flex-start;
          justify-content: flex-start;
          height: 13.5%;

          .time {
            width: 50px;
            height: 18px;
            border-top: 1px solid #f8f8f9;
            border-bottom: 1px solid #f8f8f9;
            display: flex;
            align-items: center;
            justify-content: center;

            p {
              margin: 0;
              font-weight: 600;
              font-size: 14px;
              line-height: 16px;
              text-align: center;
              letter-spacing: 0.05em;
              color: #c3c7d1;
            }
          }

        }

      }


      .schedules_calendar_card {
        position: absolute;
        top: 0;
        left: 50px;
        width: calc(100% - 50px);
        height: calc(100% - 106px);
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        border: 1px solid #dcdee4;


        .card__user-wrapper {
          width: 100%;
          height: 13%;
          display: flex;
          column-gap: 10px;
          padding: 4px 30px 4px 4px;

          .card__user-wrapper-item {
            width: 20%;
            background-color: #f2f6fa;
            border-radius: 3px;
            padding: 8px;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(2, 1fr);
            grid-column-gap: 2px;

            .item__user {
              background-color: #ffffff;
              border: 1px solid #e5e8ef;
              border-radius: 3px;
              padding: 4px;

              p {
                margin: 0;
                font-weight: 600;
                font-size: 10px;
                line-height: 12px;
                letter-spacing: 0.08em;
                text-transform: uppercase;
                color: #1f2128;
              }

              span {
                font-weight: 400;
                font-size: 10px;
                line-height: 12px;
                color: #676F84;
              }
            }
          }
        }

        .card__user-wrapper-line {
          width: 100%;
          height: 18px;
          border-top: 1px solid #f8f8f9;
          border-bottom: 1px solid #f8f8f9;
        }

      }

    }
  }

}

