.period-report {
  height: 100%;

  .period-report__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 20px 12px 20px;
    box-shadow: inset 0px -1px 0px rgba(21, 24, 48, 0.08);


    .top_select {
      display: flex;

      .select_wrapper {
        width: 180px;
        position: relative;

        .select {
          display: flex;
          align-items: center;
          border: 1px solid #f2f6fa;
          border-radius: 6px;
          padding: 6px 8px;

          .select__dropdown {
            width: 100%;
            margin: 0 10px 0 10px;

            span {
              font-weight: 400;
              font-size: 10px;
              line-height: 12px;
              letter-spacing: 0.12em;
              text-transform: uppercase;
              color: var(--grey-color);
            }

            p {
              margin: 0;
              font-weight: 600;
              font-size: 12px;
              line-height: 16px;
              letter-spacing: 0.02em;
              color: #00154a;
            }

          }

          &:hover {
            cursor: pointer;
          }
        }

        .select_option24 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option25 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option26 {
          position: absolute;
          border-right: 1px solid #f2f6fa;
          border-left: 1px solid #f2f6fa;
          border-bottom: 1px solid #f2f6fa;
          width: 100%;
          top: 100%;
          bottom: -100%;
          height: auto;
          left: 0;
          z-index: 10;
          visibility: hidden;
          background-color: #fff;

          .select_option_item {
            background-color: #fff;
          }

          p {
            margin: 0;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.02em;
            color: #00154a;
            padding: 15px 10px 10px 24px;

            &:hover {
              cursor: pointer;
            }
          }
        }

        .select_option_active {
          visibility: visible;
        }

        &:not(:last-child) {
          margin-right: 12px;
        }

      }

    }

  }

  .period-report__wrapper-card {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 5px;
    grid-row-gap: 10px;
    margin-top: 18px;
    padding: 0 20px 10px 20px;
    height: calc(100% - 105px);

    .period-report__wrapper-card-item {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      border: 1px solid #eef0f3;
      border-radius: 4px;

      .card__item-head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #eef0f3;
        padding: 5px 15px;

        .card__item-left {
          p {
            margin: 0;
            font-weight: 600;
            font-size: 12px;
            line-height: 20px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #00288d;

          }
        }


        .card__item-right {
          display: flex;
          align-items: center;

          img {
            &:hover {
              cursor: pointer;
            }

            &:not(:last-child) {
              margin-right: 20px;
            }
          }
        }

      }

      .card__item-body {
        border: 1px solid #eef0f3;
        border-radius: 4px;

        .card__item-body-item {
          display: flex;
          width: 100%;
          padding: 4px 8px;
          box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);

          .card__item-body-item-left {
            width: 85%;

            p {
              margin: 0;
              font-weight: 400;
              font-size: 14px;
              line-height: 20px;
              color: #1f2128;
            }
          }

          .card__item-body-item-right {
            width: 15%;

            p {
              margin: 0;
              font-weight: 400;
              font-size: 14px;
              line-height: 20px;
              color: #1f2128;
            }
          }

          &:nth-child(2n) {
            background: #f8f8f9; /* Цвет фона */
          }
        }

      }

      .card__item-footer {
        padding: 5px;
        text-align: right;

        a {
          font-weight: 400;
          font-size: 11px;
          line-height: 16px;
          letter-spacing: 0.04em;
          text-decoration-line: underline;
          color: #0047F9;
        }
      }

    }

  }


  @media screen and (max-width: 1366px) {
    .period-report__wrapper-card {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows:auto;
    }
  }

}

@media screen and (max-width: 1366px) {
  .period-report {
    overflow-y: scroll;
    height: calc(100% - 40px);
  }
}
