.edit-employee {
  background-color: #f5f7fa;
  height: 100%;
  padding: 25px 30px 0 20px;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

  }

  .edit-employee__top_left {
    display: flex;
    align-items: center;

    ul {
      padding: 0;
      margin: 0 0 10px 0;
      list-style: none;
      display: flex;
      align-items: center;

      li {
        &:not(:last-child) {
          margin-right: 5px;
        }

        a {
          font-weight: 500;
          font-size: 10px;
          line-height: 20px;
          letter-spacing: 0.02em;
          text-transform: uppercase;
          color: var(--grey-color);
        }
      }

      .active_link {
        a {
          font-weight: 700;
          color: var(--grey-colorfour);
        }
      }
    }
  }

  .edit-employee__top_right {
    .btn_delet {
      background: #f54940;
      border-radius: 4px;
      font-weight: 700;
      font-size: 12px;
      line-height: 20px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: var(--light-color);
      border: none;
      padding: 8px 16px;
      display: flex;
      align-items: center;

      &:hover {
        cursor: pointer;
      }

      span {
        line-height: 100%;
        margin-right: 8px;
      }
    }
  }

  .edit-employee_wrapper {
    display: flex;


    .edit-employee__user {
      display: flex;
      width: 100%;
      border: 1px solid rgba(0, 0, 0, 0.06);
      background-color: var(--light-color);

      .user__card {
        border-right: 1px solid #d5def4;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 40px 25px;
        width: 20%;

        .user__photo {
          width: 120px;
          height: 120px;
          border: 1px solid #f8f8f9;
          overflow: hidden;

          img {
            width: 100%;
            object-fit: cover;
          }
        }

        .user__name {
          text-align: center;

          h2 {
            margin: 17px 0 0 0;
            font-weight: 600;
            font-size: 18px;
            line-height: 24px;
            color: #2e303a;
          }
        }

        .user__edit {
          margin: 18px 0 0 0;
          display: flex;
          align-items: center;

          span {
            &:not(:last-child) {
              margin-right: 15px;
            }

            &:hover {
              cursor: pointer;
            }
          }
        }
      }

      @media screen and (max-width: 1366px) {
        .user__card {
          padding: 20px;
        }
      }

      .user__info {
        width: 80%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .user__info_wrap {
          flex: 1;
          display: flex;
          flex-direction: column;
          padding: 12px 12px 0 12px;
          box-shadow: inset 1px -1px 0px rgba(0, 0, 0, 0.06);

          .user__info_title {
            font-weight: 600;
            font-size: 10px;
            line-height: 20px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: var(--grey-color);
          }

          .user__name {
            display: flex;

            .user__name_item {
              display: flex;

              span {
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                color: var(--grey-color);
              }

              p {
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                margin: 0 5px 0 5px;
                color: #4a505e;
              }
            }

          }

          .name {
            .user__name_item {
              .blue__color {
                color: #0066ff;
              }


              &:not(:last-child) {
                margin-right: 68px;
              }
            }
          }

          .balance {
            .user__name_item {


              .red__color {
                color: #f54940
              }

              &:not(:last-child) {
                margin-right: 112px;
              }

              .user__name_item_eye {
                margin-left: 5px;

                &:hover {
                  cursor: pointer;
                }
              }

            }
          }

          .nurse {
            .user__name_item {
              &:not(:last-child) {
                margin-right: 55px;
              }
            }
          }
        }
      }

    }

  }

  .edit-employee_history {
    display: flex;
    flex-direction: column;
    margin-top: 12px;
    height: 100%;

    .history_tabs {
      display: flex;
      margin-left: 15px;

      .history_text {
        padding: 8px 80px;
        background: var(--light-color);
        border-radius: 4px 4px 0 0;

        p {
          margin: 0;
          font-weight: 600;
          font-size: 14px;
          line-height: 24px;
          letter-spacing: 0.02em;
          text-transform: uppercase;
          color: var(--grey-color);
        }

        &.active {
          background: var(--blue-color);

          p {
            color: var(--light-color);
          }
        }

      }
    }

    .edit-employee_history-bottom {
      display: flex;
      background-color: var(--light-color);
      border: 1px solid #dcdee4;
      border-radius: 3px;
      height: calc(100% - 390px);

      .edit-employee_history-bottom-left {
        width: 50%;
        border-right: 1px solid #dcdee4;
        padding: 8px 15px;


        .bottom-title {
          p {
            font-weight: 600;
            font-size: 10px;
            line-height: 20px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            margin: 0;
            color: #adb2bf;
          }
        }

        .bottom-left_info {
          display: flex;
          align-items: center;
          width: 100%;
          margin-top: 12px;

          span {
            font-weight: 600;
            font-size: 14px;
            line-height: 24px;
            display: flex;
            align-items: center;
          }

          .text_grey {
            color: #999faf;
            width: 20%;
          }

          .text_blue {
            color: #0066ff;
            margin-left: 20px;
          }

          .text_black {
            color: #1f2128;
            margin-left: 20px;
          }
        }
      }

      .edit-employee_history-bottom-right {
        width: 50%;
        padding: 8px 15px;

        .bottom-title {
          p {
            font-weight: 600;
            font-size: 10px;
            line-height: 20px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            margin: 0;
            color: #adb2bf;
          }
        }

        .bottom-tag {
          display: flex;
          flex-direction: column;
          flex-wrap: wrap;
          gap: 10px;
          margin-top: 18px;
          padding-left: 60px;
          height: calc(100% - 35px);
          overflow-y: scroll;

          &::-webkit-scrollbar {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
            border-radius: 10px;
            background-color: #fff;
          }

          &::-webkit-scrollbar {
            width: 8px;
            background-color: var(--light-color);
          }

          &::-webkit-scrollbar-thumb {
            border-radius: 10px;
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
            background-color: var(--grey-color);
          }

          span {
            display: block;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            color: #00154a;
            flex-basis: 5%;
          }
        }
      }


    }
  }


