        margin: 0;
        font-weight: 600;
        font-size: 17px;
        line-height: 24px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: var(--balck-color);
      }

      img {
        cursor: pointer;
      }
    }

    .modal {
      background-color: var(--light-color);
      width: 570px;
      display: none;
    }

    .modal-overlay--visible {
      opacity: 1;
      visibility: visible;
      transition: all 0.3s ease-in-out;
    }

    .modal--visible {
      display: block;
    }

