.edit-patients {
  height: 100%;
  background-color: #F8F8F9;
  padding: 0 30px 0 20px;

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

  }

  .edit-patients__top_left {
    display: flex;
    align-items: center;

    ul {
      margin: 0;
      padding: 12px 0 12px 0;
      list-style: none;
      display: flex;
      align-items: center;

      li {
        &:not(:last-child) {
          margin-right: 5px;
        }

        a {
          font-weight: 500;
          font-size: 10px;
          line-height: 20px;
          letter-spacing: 0.02em;
          text-transform: uppercase;
          color: var(--grey-color);
        }
      }

      .active_link {
        a {
          font-weight: 700;
          color: var(--grey-colorfour);
        }
      }
    }
  }

  .edit-patients__top_right {
    .btn_delet {
      background: #f54940;
      border-radius: 4px;
      font-weight: 700;
      font-size: 12px;
      line-height: 20px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      color: var(--light-color);
      border: none;
      padding: 8px 16px;
      display: flex;
      align-items: center;

      &:hover {
        cursor: pointer;
      }

      span {
        line-height: 100%;
        margin-right: 8px;
      }
    }
  }

  .edit-patients__wrapper {
    display: flex;

    .edit-patients__user {
      display: flex;
      width: 100%;
      border: 1px solid rgba(0, 0, 0, 0.06);
      background-color: var(--light-color);

      .user__card {
        border-right: 1px solid #d5def4;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 40px 25px;
        width: 30%;

        .user__photo {
          width: 120px;
          height: 120px;
          border: 1px solid #f8f8f9;
          border-radius: 8px;
          overflow: hidden;

          img {
            width: 100%;
            object-fit: cover;
          }
        }

        .user__name {
          h2 {
            margin: 17px 0 0 0;
            font-weight: 600;
            font-size: 18px;
            line-height: 24px;
            color: #2e303a;
          }
        }

        .user__edit {
          margin: 18px 0 0 0;
          display: flex;
          align-items: center;

          span {
            &:not(:last-child) {
              margin-right: 15px;
            }

            &:hover {
              cursor: pointer;
            }
          }
        }
      }

      @media screen and (max-width: 1366px) {
        .user__card {
          padding: 30px 15px;
        }
      }

      .user__info {
        width: 70%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        .user__info_wrap {
          flex: 1;
          display: flex;
          flex-direction: column;
          padding: 12px 12px 0 12px;
          box-shadow: inset 1px -1px 0px rgba(0, 0, 0, 0.06);

          .user__info_title {
            font-weight: 600;
            font-size: 10px;
            line-height: 20px;
            letter-spacing: 0.02em;
            text-transform: uppercase;
            color: var(--grey-color);
          }

          .user__name {
            display: flex;

            .user__name_item {
              display: flex;

              span {
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                color: var(--grey-color);
              }

              p {
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                margin: 0 0 0 5px;
                color: #4A505E;
              }
            }

          }

          .name {
            .user__name_item {

              .blue__color {
                color: #0066ff;
              }

              &:not(:last-child) {
                margin-right: 68px;
              }
            }

            @media screen and (max-width: 1366px) {
              .user__name_item {
                &:not(:last-child) {
                  margin-right: 50px;
                }
              }
            }
          }

          .balance {
            .user__name_item {


              .red__color {
                color: #f54940
              }

              &:not(:last-child) {
                margin-right: 112px;
              }

            }

            @media screen and (max-width: 1366px) {
              .user__name_item {

                &:not(:last-child) {
                  margin-right: 60px;
                }

              }
            }
          }

          .nurse {
            .user__name_item {
              &:not(:last-child) {
                margin-right: 55px;
              }
            }
          }
        }
      }


    }

    @media screen and (max-width: 1366px) {
      .edit-patients__user {
        width: 80%;
      }
    }

    .user__note {
      width: 24%;
      margin-left: 5px;
      background-color: #fdf3d8;
      border: 1px solid #d5def4;
      border-radius: 4px;
      padding: 15px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .user__note_top {

        .user__note__title {
          display: flex;
          align-items: center;
          justify-content: space-between;

          h2 {
            font-weight: 600;
            font-size: 12px;
            line-height: 24px;
            letter-spacing: 0.12em;
            text-transform: uppercase;
            color: #676F84;
            margin: 0;
          }
        }

        .user__note__input {
          input {
            border: none;
            background-color: transparent;
            font-weight: 400;
            font-size: 14px;
            line-height: 24px;
            color: #2e303a;
            width: 100%;
            outline: none;
            opacity: 0.5;
            padding: 0;

            &::placeholder {
              font-weight: 400;
              font-size: 14px;
              line-height: 24px;
              color: #2e303a;
              opacity: 0.5;
            }
          }
        }
      }

      .user__note_bottom {

        .user__note__date {
          display: flex;
          justify-content: space-between;
          margin-top: auto;

          span {
            font-weight: 400;
            font-size: 10px;
            line-height: 10px;
            color: #1f2128;
            opacity: 0.2;
          }
        }
      }
    }


    @media screen and (max-width: 1366px) {
      .user__note {
        padding: 10px;
      }
    }

  }

  .edit-patients_wrapper-none {
    display: none;
  }

  .edit-patients__history {
    display: flex;
    flex-direction: column;
    margin-top: 12px;
    height: 100%;

    .history_tabs {
      display: flex;

      .history_text {
        padding: 8px 80px;
        background: var(--light-color);
        border-radius: 4px 4px 0 0;

        p {
          margin: 0;
          font-weight: 600;
          font-size: 14px;
          line-height: 24px;
          letter-spacing: 0.02em;
          text-transform: uppercase;
          color: var(--grey-color);
        }

        &.active {
          background: var(--blue-color);

          p {
            color: var(--light-color);
          }
        }

        &:hover {
          cursor: pointer;
        }

        &:not(:last-child) {
          margin-right: 5px;
        }
      }

      @media screen and (max-width: 1366px) {
        .history_text {
          padding: 8px 60px;
        }
      }
    }

    .tab {
      position: relative;
      display: none;
      margin-top: 8px;
    }

    .tab-2 {
      height: calc(100% - 104px);
    }

    .is-active {
      display: block;
    }

    .edit-patients__table {
      position: relative;
      height: calc(100% - 104px);
      overflow-y: auto;
      padding-bottom: 50px;

      &::-webkit-scrollbar {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        border-radius: 10px;
        background-color: #fff;
      }

      &::-webkit-scrollbar {
        width: 8px;
        background-color: var(--light-color);
      }

      &::-webkit-scrollbar-thumb {
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
        background-color: var(--grey-color);
      }

      .tex_grey {
        color: #ADB2BF;
      }

      .tex_blue {
        color: #0066ff;
        display: inline-block;
        margin-right: 2px;
      }

      table {
        width: 100%;
        background-color: var(--light-color);

        tr {
          &:hover {
            background-color: #f9fafc;
            cursor: pointer;
          }
        }

        td {
          padding: 15px;

          p {
            margin: 0;
            font-weight: 400;
            font-size: 14px;
            line-height: 115%;
            letter-spacing: 0.01em;
            color: var(--text-table);
          }
        }


        .filter_text {
          display: flex;
          align-items: center;

          span {
            font-weight: 700;
            font-size: 11px;
            line-height: 16px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: var(--grey-colortwo);
          }

          .filter_img {
            display: flex;
            align-items: center;
            flex-direction: column;
            margin-left: 3px;

            img {
              padding: 1px;

              &:not(:last-child) {
                margin-bottom: 1px;
              }

              &:hover {
                cursor: pointer;
              }
            }
          }
        }

      }

    }

    .edit-patients__pagination {
      position: absolute;
      bottom: 104px;
      left: 0;
      width: 100%;
      background: rgba(244, 247, 252, 0.75);
      backdrop-filter: blur(8px);
      padding: 13px 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .pagination__left {
        span {
          font-weight: 500;
          font-size: 12px;
          line-height: 18px;
          letter-spacing: 0.03em;
          color: var(--grey-colorthree);
        }
      }

      .pagination__right {
        display: flex;
        align-items: center;

        .pagination_right_select {
          margin-right: 25px;

          span {
            font-weight: 500;
            font-size: 12px;
            line-height: 18px;
            letter-spacing: 0.03em;
            color: var(--grey-colorthree);
          }

          select {
            background-color: transparent;
            border: none;
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 0.03em;
            color: var(--grey-colorthree);
            outline: none;
          }
        }

        .pagination_right_btn {
          display: flex;
          align-items: center;

          .pagination_right_btn_number {
            margin: 0 10px;

            span {
              font-weight: 500;
              font-size: 12px;
              line-height: 18px;
              letter-spacing: 0.03em;
              color: #171C26;
            }
          }


          button {
            background-color: #f7f9fc;
            box-shadow: 0 0 0 1px rgba(70, 79, 96, 0.24);
            border-radius: 6px;
            border: none;
            width: 24px;
            height: 20px;
            display: flex;
            align-items: center;
            justify-content: center;


            &:hover {
              cursor: pointer;
            }

          }


          .button_active {
            background-color: #fff;
          }
        }
      }
    }

    //

    .patients__none {
      display: none !important;
    }

    .accounts-patients {
      display: flex;
      grid-column-gap: 20px;
      height: calc(100% - 30px);

      .accounts-patients_left {
        width: 20%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        background-color: #fff;

        .accounts-patients_left_top {
          flex: 1 1 auto;

          .accounts-patients_left_head {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 5px 15px;
            background-color: #eef0f3;


            p {
              margin: 0;
              font-weight: 600;
              font-size: 12px;
              line-height: 20px;
              letter-spacing: 0.04em;
              text-transform: uppercase;
              color: var(--blue-table-color);
            }

            img {
              &:hover {
                cursor: pointer;
              }
            }
          }

          .accounts-patients_left_table {

            .table-head {
              display: flex;
              align-items: center;
              background-color: #f8f8f9;

              span {
                flex: 1;
                font-weight: 600;
                font-size: 11px;
                line-height: 16px;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: #757d93;
                padding: 4px 8px;

                &:not(:last-child) {
                  border-right: 1px solid #e8e8ee;
                }
              }
            }

            .table-body {
              background-color: #ffffff;
              border-bottom: 1px solid #f8f8f9;
              display: flex;
              align-items: flex-end;

              .body-item {
                flex: 1;
                padding: 3px;

                .tex_red {
                  color: #cc3030;
                }

                span {
                  font-weight: 400;
                  font-size: 11px;
                  line-height: 20px;
                  color: #1f2128;
                  display: block;
                }
              }
            }

          }

          .accounts-patients_left_add {
            display: flex;
            justify-content: center;
            margin-top: 20px;

            button {
              width: 40px;
              height: 40px;
              background-color: #f2f6fa;
              border-radius: 50%;
              border: none;

              &:hover {
                cursor: pointer;
              }
            }
          }

          .accounts-patients_left_top_bottom {
            height: 100%;
            background-color: #eef0f3;
            padding: 12px;
            margin-bottom: 4px;

            .top_bottom_item {
              display: flex;
              justify-content: space-between;
              align-items: flex-start;

              span {
                font-weight: 600;
                font-size: 11px;
                line-height: 20px;
                color: #1f2128;
              }
            }

          }

          &:not(:last-child) {
            padding-bottom: 5px;
          }
        }


      }

      .accounts-patients_right {
        width: 80%;
        height: 100%;
        display: flex;
        flex-direction: column;

        .accounts-patients_right_head {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 5px 15px;
          background-color: #eef0f3;


          p {
            margin: 0;
            font-weight: 600;
            font-size: 12px;
            line-height: 20px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: var(--blue-table-color);
          }

          img {
            &:hover {
              cursor: pointer;
            }
          }
        }

        .accounts-patients_right_user {
          display: flex;
          align-items: center;
          justify-content: space-between;
          background-color: var(--light-color);
          padding: 12px;

          .accounts-patients_right_user_info {
            display: flex;
            align-items: center;
            flex: 1 1 auto;

            .user-left_item {
              display: flex;
              align-items: flex-start;
              flex: 1;

              .user-left_item_img {
                width: 40px;
                height: 40px;
                background-color: var(--light-color);
                border: 1px solid #f8f8f9;
                border-radius: 8px;
                overflow: hidden;

                img {
                  display: block;
                  width: 100%;
                  height: 100%;
                  object-fit: cover;
                }
              }

              .user-left_item_info {
                margin-left: 8px;

                span {
                  display: block;
                  font-weight: 600;
                  font-size: 10px;
                  line-height: 20px;
                  letter-spacing: 0.02em;
                  text-transform: uppercase;
                  color: var(--grey-color);
                }

                h3 {
                  font-weight: 400;
                  font-size: 14px;
                  line-height: 20px;
                  margin: 0;
                  color: #4A505E;
                }
              }
            }
          }

        }

        .accounts-patients_right_checkbox {
          display: flex;
          align-items: center;
          background-color: #f8f8f9;
          padding: 4px 15px;

          .checkbox_info {
            display: flex;
            align-items: center;
            flex: 1;

            h3 {
              margin: 0;
              font-weight: 600;
              font-size: 12px;
              line-height: 20px;
              letter-spacing: 0.04em;
              text-transform: uppercase;
              color: var(--blue-table-color);
            }

            .checkbox_select_all {
              a {
                font-weight: 400;
                font-size: 12px;
                line-height: 20px;
                text-decoration-line: underline;
                text-transform: uppercase;
                color: var(--blue-color);
              }
            }

            .checkbox_toggle {
              display: flex;
              align-items: center;

              span {
                display: flex;
                align-items: center;
                font-weight: 600;
                font-size: 12px;
                line-height: 40px;
                letter-spacing: 0.12em;
                text-transform: uppercase;
                color: #adb2bf;
                margin-left: 5px;
              }

              input[type=checkbox] {
                height: 0;
                width: 0;
                visibility: hidden;
              }

              label {
                cursor: pointer;
                text-indent: -9999px;
                width: 42px;
                height: 24px;
                display: block;
                border-radius: 100px;
                border: 1px solid #dcdee4;
                background-color: #f8f8f9;
                position: relative;
                margin-left: 15px;
              }

              label:after {
                content: '';
                position: absolute;
                top: 1px;
                left: 2px;
                width: 20px;
                height: 20px;
                background-color: #fff;
                border-radius: 90px;
                transition: 0.3s;
              }

              input:checked + label {
                background: #003FDE;
                border-radius: 20px;
              }

              input:checked + label:after {
                left: calc(100% - 1px);
                transform: translateX(-100%);
              }

            }
          }

          .checkbox_info_end {
            display: flex;
            justify-content: flex-end;
          }
        }

        .accounts-patients_right_tooth {
          padding: 20px;
          background-color: var(--light-color);
          display: flex;
          justify-content: space-evenly;
          align-items: center;

          .right_tooth_icon_wrapper {
            display: flex;
            flex-wrap: wrap;
            width: calc(25% - 10px);
            gap: 10px;

            .right-tooth_icon {
              width: 32px;
              position: relative;

              span {
                font-weight: 500;
                font-size: 10px;
                line-height: 20px;
                color: #757d93;
                transform: translateY(-33px) translateX(7px);
                display: block;
              }

              .right-tooth_icon__top {
                width: 32px;
                height: 32px;

              }

              .right-tooth_icon__bottom {
                bottom: -12px;
                height: 100%;
                right: -5px;
                position: absolute;

                svg {
                  width: 25px;
                  height: 25px;
                  background-color: var(--light-color);
                }
              }
            }

            .active {
              svg {
                fill: #003FDE;

                path {
                  stroke: #003FDE;
                }
              }

              span {
                color: #fff;
              }
            }
          }

          @media screen and (max-width: 1366px) {
            .right_tooth_icon_wrapper {
              width: calc(40% - 10px);
            }
          }


        }

        .accounts-patients_right_services {
          height: 50%;
          background-color: var(--light-color);

          .right_services_head {
            display: flex;
            align-items: center;
            background-color: #f8f8f9;

            .right_services_head_item {
              flex: 1;
              padding: 4px 8px;

              span {
                font-weight: 600;
                font-size: 11px;
                line-height: 16px;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: #757d93;
              }
            }
          }

          .right_services_body {
            display: flex;
            flex-direction: column;
            overflow-y: scroll;
            height: calc(100% - 150px);

            .right_services_body_row {
              display: flex;
              align-items: center;
              background-color: var(--light-color);
              box-shadow: inset 0px -1px 0px #f8f8f9;
            }

            .right_services_body_item {
              display: flex;
              align-items: center;
              flex: 1 100px;
              padding: 4px 8px;

              .body_item_btn {
                width: 24px;
                height: 24px;
                background-color: #f8f8f9;
                border-radius: 25px;
                display: flex;
                align-items: center;
                justify-content: center;

                &:hover {
                  cursor: pointer;
                }
              }

              .body_item_number {
                margin: 0 10px 0 10px;
                font-weight: 400;
                font-size: 14px;
                line-height: 20px;
                color: #1f2128;
                display: block;
              }

              span {
                font-weight: 400;
                font-size: 14px;
                line-height: 24px;
                color: #1F2128;

              }
            }

            &::-webkit-scrollbar {
              display: none;
            }

            @media screen and (max-width: 1366px) {
              .right_services_body_item {
                span {
                  font-size: 12px;
                }
              }
            }

            span {
              font-weight: 600;
              font-size: 11px;
              line-height: 16px;
              letter-spacing: 0.04em;
              text-transform: uppercase;
              color: #757d93;
            }
          }


          .right_services_body_add {
            display: flex;
            justify-content: center;
            margin-top: 30px;

            button {
              padding: 12px 20px;
              background-color: #003fde;
              border-radius: 3px;
              font-weight: 600;
              font-size: 12px;
              line-height: 14px;
              letter-spacing: 0.08em;
              text-transform: uppercase;
              border: none;
              color: var(--light-color);

              img {
                margin-left: 10px;
              }

              &:hover {
                cursor: pointer;
              }
            }
          }

          @media screen and (max-width: 1366px) {
            .right_services_body_add {
              margin-top: 15px;
            }
          }


        }

        .right_services_body_footer {
          display: flex;
          flex-direction: column;

          .footer_info {
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: space-between;
            padding: 12px;
            background-color: #eef0f3;

            .footer_info_item {
              display: flex;
              justify-content: space-between;
              width: 100%;

              &:not(:last-child) {
                margin-bottom: 8px;
              }
            }

            span {
              font-size: 14px;
              line-height: 20px;
              color: #1f2128;
            }

            .footer_info_text_500 {
              font-weight: 500;
            }

            .footer_info_text_800 {
              font-weight: 800;
            }
          }

          .footer__btn {
            display: flex;
            justify-content: space-between;
            padding: 15px 0 15px 0;

            button {
              border-radius: 3px;
              font-weight: 600;
              font-size: 12px;
              line-height: 16px;
              letter-spacing: 0.08em;
              padding: 12px 40px;
              cursor: pointer;
              text-transform: uppercase;
            }

            .btn_outline {
              border: 1px solid #dcdee4;
              color: #676f84;
            }

            .btn_blue {
              background-color: #00288d;
              border-radius: 3px;
