            font-size: 11px;
            line-height: 20px;
            letter-spacing: 0.08em;
            color: #adb2bf;

            &::placeholder {
              font-weight: 500;
              font-size: 11px;
              line-height: 20px;
              letter-spacing: 0.08em;
              text-transform: uppercase;
              color: #adb2bf;
            }
          }
        }

        .modal__body_accordion {
          margin-top: 10px;

          .accordions {
            width: 100%;
          }

          .accordion {
            width: 100%;
            height: auto;

            &:not(:last-child) {
              margin-bottom: 5px;
            }
          }

          .accordion__slot {
            width: 100%;
            display: block;

            .accordion_icon {
              transform: rotate(360deg);
            }
          }

          .accordion__button {
            cursor: pointer;
            border-radius: 3px;
            padding: 12px 20px 12px 12px;
            width: 100%;
            text-align: left;
            border: none;
            outline: none;
            display: flex;
            justify-content: space-between;
            align-items: center;
            transition: color 500ms ease;
            user-select: none;
            background-color: #fafafc;
            position: relative;
            font-weight: 600;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.06em;
            text-transform: uppercase;
            color: #1f2128;

          }

          .accordion__button::after {
            transition: transform 500ms;
            width: 15px;
            height: 15px;
            display: block;
            position: absolute;
          }

          .accordion__button-active::after {
            transform: translate(-50%, -50%) rotate(90deg);
          }

          .accordion__button-active {
            color: #f2f6fa;
            background-color: #0038c6;

            .accordion_icon {
              transform: rotate(180deg) !important;

              svg {
                path {
                  stroke: #fff;
                }
              }
            }
          }

          .accordion__panel {
            overflow: hidden;
            max-height: 0;
            visibility: hidden;
            transition: visibility 500ms, max-height 600ms;
            transition-timing-function: cubic-bezier(0.694, 0.0482, 0.335, 1);

            .accordion__panel_item {
              display: flex;
              align-items: center;
              padding: 12px 10px 12px 10px;

              .accordion__panel_item_img {
                margin-right: 10px;
              }

              .accordion__panel_item_info {
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;
                padding-left: 20px;

                p {
                  margin: 0;
                  font-size: 14px;
                  line-height: 16px;
                  letter-spacing: 0.02em;
                  color: #1f2128;
                  display: block;
                }

                .text__bold {
                  font-weight: 600;
                  width: 25%;
                  text-align: right;
                }

                .info__text {
                  width: 75%;
                }
              }

              &.active {
                background-color: #edfbf2;

                .accordion__panel_item_info {
                  padding-left: 0;
                }
              }
            }
          }

          .accordion__panel-active {
            max-height: var(--height, 0);
            visibility: visible;
          }

        }
      }
    }

