        box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.08);
        padding: 15px 25px;
        display: flex;
        justify-content: flex-end;

        button {
          background-color: #00288d;
          border-radius: 3px;
          padding: 15px 20px;
          font-weight: 600;
          font-size: 13px;
          line-height: 15px;
          letter-spacing: 0.08em;
          text-transform: uppercase;
          color: var(--light-color);
        }
      }
    }

    .modal-overlay {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.7);
      display: flex;
      align-items: center;
      justify-content: flex-end;
      height: 100vh;
      width: 100%;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease-in-out;
      z-index: 999;

      h2 {
        margin: 0;
        font-weight: 600;
        font-size: 17px;
        line-height: 24px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: var(--balck-color);
      }

      img {
        cursor: pointer;
      }

      .modal--1 {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
      }
    }

    .modal {
      background-color: var(--light-color);
      width: 570px;
      height: 100%;
      display: none;
    }

    .modal-overlay--visible {
      opacity: 1;
      visibility: visible;
      transition: all 0.3s ease-in-out;
    }

    .modal--visible {
      display: block;
    }


  }

}
