/*Show navbar movil*/
.show {
  left: 0;
}

/*Add padding body movil*/
.body-pd {
  padding-left: calc(var(--nav-width) + 1rem);
}


/* ===== MEDIA QUERIES=====*/
@media screen and (min-width: 768px) {
  body {
    margin: calc(var(--header-height) + 1rem) 0 0 0;
    padding-left: calc(var(--nav-width) + 2rem);
  }
  .header {
    height: calc(var(--header-height) + 1rem);
    padding: 0 2rem 0 calc(var(--nav-width) + 2rem);

    &__img {
      width: 40px;
      height: 40px;

      & img {
        width: 45px;
      }
    }
  }

  .l-navbar {
    left: 0;
    padding: 1rem 0 0 0;
  }

  /*Show navbar desktop*/
  .show {
    width: calc(var(--nav-width) + 156px);
  }

  /*Add padding body desktop*/
  .body-pd {
    padding-left: calc(var(--nav-width) + 188px);

    .edit-patients .edit-patients__history .accounts-patients .accounts-patients_right .accounts-patients_right_tooth .right_tooth_icon_wrapper {
      width: calc(45% - 10px);
    }
  }

}

.wrapper__box {
  margin-top: 15px;
  height: 100%;
}

.btn_top {
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: var(--light-color);
  border: none;
  padding: 12px 18px 12px 24px;

  span {
    margin-left: 10px;
  }

  &:hover {
    cursor: pointer;
  }
}

.btn_orange {
  background-color: #ff6700;
  box-shadow: 0 15px 25px -8px rgba(255, 103, 0, 0.25);
  border-radius: 6px;
}

.btn_blue {
  background-color: var(--blue-color);
  border-radius: 2px;
}

.time__active {
  color: #27a55d !important;
}

// login page
.login__wrapper {
  position: absolute;
  top: -48px;
  left: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: calc(100% + 48px);
  background-image: url("../img/loginBg.svg");
  background-repeat: no-repeat;
  background-position: top center;

  .login__wrapper_logo {
    padding-top: 100px;
  }

  .login__wrapper_form {
    width: 480px;
    background-color: var(--light-color);
    box-shadow: 0 25px 15px -20px rgba(13, 40, 108, 0.17), 0px 23px 35px rgba(40, 90, 216, 0.15);
    border-radius: 12px;

    h2 {
      margin: 0;
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: #00154a;
      padding: 15px 25px;
    }

    form {
      box-shadow: inset 0px -1px 0px rgba(0, 0, 0, 0.08);
      padding: 25px 20px;

      label {
        display: flex;
        flex-direction: column;
        font-weight: 600;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 0.12em;
        text-transform: uppercase;
        color: #999faf;
        margin-bottom: 15px;

        input {
          padding: 12px 15px;
          border: 1px solid rgba(0, 40, 141, 0.08);
          border-radius: 4px;
          outline: none;
          margin-top: 5px;
          font-size: 14px;
          line-height: 20px;
          letter-spacing: 0.03em;
          color: #00154a;

          &::placeholder {
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.03em;
            color: #00154a;
          }
        }
      }

      .login__wrapper_form_link {
        font-weight: 600;
        font-size: 12px;
        line-height: 24px;
        letter-spacing: 0.12em;
        text-transform: uppercase;
        color: #0047f9;
      }
    }

    .login__wrapper_form_btn {
      display: flex;
      justify-content: flex-end;
      padding: 8px;

      button {
        background-color: #00288d;
        border-radius: 8px;
        padding: 12px 50px;
        font-weight: 600;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 0.08em;
        text-transform: uppercase;
        color: var(--light-color);
      }
    }
  }


  .login__wrapper__img {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
}




